import { useState, useEffect } from 'react'
import { constants } from 'ethers'
import { useHistory } from 'react-router-dom'
import Jumbo from '../components/Jumbo'
import { useWeb3 } from '../web3'
import { connect } from '../web3/useProvider'
import { useAddresses } from '../web3/chains'
import { useData } from '../dataContext'
import { useCatBalance } from '../dataContext/cats'
import { useOpenSeaProxyAddress, useOpenSeaApproved } from '../dataContext/openSea'
import { useDoomCatContract, useOpenSeaProxyRegistryContract } from '../dataContext/contracts'
import { useTransaction } from '../dataContext/transactions'

const Container = ({ show, completed, instructions, buttonText, buttonClick, buttonDisabled, done }) => {
  return (
    <div className="mb-8 flex relative">
      <div className={`mr-4 text-4xl absolute -left-12 ${!completed && "hidden"}`}>{done ? "" : ""}</div>
      <div>
        <div className={`${!show && "opacity-30"}`}>{instructions}</div>
        {buttonText && buttonClick && <button onClick={buttonClick} disabled={buttonDisabled} className="mt-2 bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">{buttonText}</button>}
      </div>
    </div>
  )
}

const Account = ({ account }) => {
  const [completed, setCompleted] = useState(false)
  useEffect(() => {
    setCompleted(!!account)
  }, [account])

  return (
    <Container
      show={true}
      completed={completed}
      instructions="Connect your wallet to determine if additional steps are needed to sell your DoomCats on OpenSea."
      buttonText="CONNECT"
      buttonClick={connect}
      buttonDisabled={completed}
    />
  )
}

const NeedACat = ({ account, myCatBalance }) => {
  const history = useHistory()

  const [show, setShow] = useState(false)
  useEffect(() => {
    setShow(!!account)
  }, [account])

  const [completed, setCompleted] = useState(false)
  useEffect(() => {
    setCompleted(myCatBalance.gt(0))
  }, [myCatBalance])

  return (
    <Container
      show={show}
      completed={completed}
      instructions="You need at least one (1) DoomCat to continue."
      buttonText="RESCUE"
      buttonClick={() => history.push('/')}
      buttonDisabled={!show || completed}
    />
  )
}

const Proxy = ({ myCatBalance, openSeaProxyRegistryContract, openSeaProxyAddress }) => {
  const [show, setShow] = useState(false)
  useEffect(() => {
    setShow(myCatBalance.gt(0))
  }, [myCatBalance])

  const { contractCall, pending } = useTransaction()
  const createProxy = () => {
    contractCall(
      openSeaProxyRegistryContract.registerProxy, [],
      "CREATING PROXY CONTRACT", "PROXY CONTRACT CREATION FAILED", "PROXY CONTRACT CREATION SUCCESSFUL"
    )
  }

  const [completed, setCompleted] = useState(false)
  useEffect(() => {
    setCompleted(openSeaProxyAddress !== constants.AddressZero)
  }, [openSeaProxyAddress])

  return (
    <Container
      show={show}
      completed={completed}
      instructions={<div>Create an OpenSea Proxy contract for your account. This only needs to happen once per account, in order to list <span className="italic">anything</span> on OpenSea.</div>}
      buttonText="CREATE PROXY"
      buttonClick={createProxy}
      buttonDisabled={!show || completed || pending}
    />
  )
}

const Approved = ({ openSeaProxyAddress, doomCatContract, openSeaApproved }) => {
  const [show, setShow] = useState(false)
  useEffect(() => {
    setShow(openSeaProxyAddress !== constants.AddressZero)
  }, [openSeaProxyAddress])

  const { contractCall, pending } = useTransaction()
  const approveProxy = () => {
    contractCall(
      doomCatContract.setApprovalForAll, [openSeaProxyAddress, true],
      "APPROVING PROXY CONTRACT", "PROXY CONTRACT APPROVAL FAILED", "PROXY CONTRACT APPROVAL SUCCESSFUL"
    )
  }

  const [completed, setCompleted] = useState(false)
  useEffect(() => {
    setCompleted(openSeaApproved)
  }, [openSeaApproved])

  return (
    <Container
      show={show}
      completed={completed}
      instructions="Submit an Approval transaction to enable selling your DoomCats on OpenSea."
      buttonText="APPROVE"
      buttonClick={approveProxy}
      buttonDisabled={!show || completed || pending}
    />
  )
}

const Congrats = ({ openSeaApproved }) => {
  const [show, setShow] = useState(false)
  useEffect(() => {
    setShow(openSeaApproved)
  }, [openSeaApproved])

  return (
    <Container
      show={show}
      completed={show}
      instructions="Congrats! Your address is approved for trading DoomCats on OpenSea."
      done={true}
    />
  )
}

const OpenSeaApproval = () => {
  const { account, chainId, provider, signerOrProvider } = useWeb3()
  const { doomCatContract, currentBlock } = useData()
  const addresses = useAddresses(chainId)
  const doomCatContractWithoutSigner = useDoomCatContract(provider, addresses)
  const myCatBalance = useCatBalance(doomCatContract, account)
  const openSeaProxyRegistryContract = useOpenSeaProxyRegistryContract(signerOrProvider, doomCatContract)
  const openSeaProxyAddress = useOpenSeaProxyAddress(openSeaProxyRegistryContract, account, currentBlock)
  const openSeaApproved = useOpenSeaApproved(doomCatContractWithoutSigner, account, myCatBalance, openSeaProxyAddress, currentBlock)

  return (
    <Jumbo>
      <div className="mb-8 text-center text-xl">
        Sell your DoomCats on OpenSea
      </div>
      <div className="pl-8">
        <Account
          account={account}
        />
        <NeedACat
          account={account}
          myCatBalance={myCatBalance}
        />
        <Proxy
          myCatBalance={myCatBalance}
          openSeaProxyRegistryContract={openSeaProxyRegistryContract}
          openSeaProxyAddress={openSeaProxyAddress}
        />
        <Approved
          openSeaProxyAddress={openSeaProxyAddress}
          doomCatContract={doomCatContract}
          openSeaApproved={openSeaApproved}
        />
        <Congrats
          openSeaApproved={openSeaApproved}
        />
      </div>
    </Jumbo>
  )
}

export default OpenSeaApproval
